export default class SearchServices {
static async schoolsSearch(data) {
  const response = await fetch(`https://u1206380.plsk.regruhosting.ru/api/schools/search`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
    },
    body: JSON.stringify(data)
  });
  return response.json();
}
static async courseTypeFilters() {
  const response = await fetch(`https://u1206380.plsk.regruhosting.ru/api/courses/courseTypeFilters`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
    },
  });
  return response.json();
}
static async subjectFilters() {
  const response = await fetch(`https://u1206380.plsk.regruhosting.ru/api/courses/subjectFilters`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
    },
  });
  return response.json();
}
}
