export default function getHelperTextProps(errors, field) {
   for (let prop in errors) {
   if (prop === field) {
      let error_text =  '';
      // alert(prop + ' = ' + obj[prop]);
      Object.keys(errors[prop]).forEach(function(error){ error_text+=errors[prop][error] + " " ;});
      return {"error":'error',  'helperText': `${error_text}`};
    }
  }
}
