export default class AuthServices {
  static async register(data) {
    const response = await fetch(
      `https://u1206380.plsk.regruhosting.ru/api/users/register`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
       body: JSON.stringify(data)
    });
    var result
    try {
     result = await response.json()
  } catch {
     result = undefined
  }
    if (result && result.token) localStorage.setItem('@usr_t', result.token)
    return [result, response];
  }

  static get isAuthenticated () {
  return !!localStorage.getItem('@usr_t')
}

  static async authenticate(data) {
    const response = await fetch(
      `https://u1206380.plsk.regruhosting.ru/api/users/authenticate`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
       body: JSON.stringify(data)
    });
    const result = await response.json()
    if (result && result.token) localStorage.setItem('@usr_t', result.token)
    return [result, response];
  }

  static logout () {
  localStorage.removeItem('@usr_t')
}

  static async dummy() {
    const response = await fetch(`https://u1206380.plsk.regruhosting.ru/api/courses`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      }
    });
    return response.json();
  }
}
