export default class SchoolServices {
static async getSchoolById(id) {
  const response = await fetch(`https://u1206380.plsk.regruhosting.ru/api/schools/${id}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
    },
  });
  return response.json();
}
static async getCourseById(id) {
  const response = await fetch(`https://u1206380.plsk.regruhosting.ru/api/courses/${id}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
    },
  });
  return response.json();
}

}
